import { createSlice } from '@reduxjs/toolkit'


export const settingsSlice = createSlice({
    name: 'settings',
    initialState: {
        columns: 10,
        rows: 5,
        tileWidth: 16,
        tileHeight: 16,
        inferenceImage: 'default',
        images: ['default'],
    },
    reducers: {
        incCol: (state) => { state.columns += 1 },
        decCol: (state) => { state.columns -= 1 },
        setCol: (state, col) => { state.columns = col.payload },
        setRow: (state, row) => { state.rows = row.payload },
        setTileWidth: (state, width) => { state.tileWidth = width.payload },
        setTileHeight: (state, height) => { state.tileHeight = height.payload },
        setImage: (state, image) => { state.inferenceImage = image.payload },
        addImage: (state, image) => { state.images.push(image.payload) },
    }
})

// actions
export const { incCol, setCol, setRow, setTileWidth, setTileHeight, setImage, addImage } = settingsSlice.actions
export default settingsSlice.reducer
